import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { FlowBoardConfiguration } from './configuration';
import { HttpClient } from '@angular/common/http';


@NgModule({
  imports:      [],
  declarations: [],
  exports:      [],
  providers: []
})
export class FlowBoardApiModule {
    public static forRoot(configurationFactory: () => FlowBoardConfiguration): ModuleWithProviders<FlowBoardApiModule> {
        return {
            ngModule: FlowBoardApiModule,
            providers: [ { provide: FlowBoardConfiguration, useFactory: configurationFactory } ]
        };
    }

    constructor( @Optional() @SkipSelf() parentModule: FlowBoardApiModule,
                 @Optional() http: HttpClient) {
        if (parentModule) {
            throw new Error('FlowBoardApiModule is already loaded. Import in your base AppModule only.');
        }
        if (!http) {
            throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
            'See also https://github.com/angular/angular/issues/20575');
        }
    }
}
